.footer__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    margin: 0;
    color: #545454;
}

@media screen and (max-width:900px) {
    .footer__text {
        padding-left: 20px;
    }
}

@media screen and (max-width:480px) {
    .footer__text {
        font-size: 14px;
        line-height: 17px;
    }
}